import React, { useCallback, useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import styled from "styled-components";
import Logo from "../images/header_logo.png";

const SLink = styled(Link)`
  text-decoration: none;
  margin-left: 60px;
  position: relative;
  color: ${(props) =>
    props.test == "/Approach" || props.test == "/Contact"
      ? props.$current
        ? "var(--key-color)"
        : "#000"
      : props.$current
      ? "var(--key-color)"
      : "#fff"};
  @media screen and (max-width: 1440px) {
    color: ${(props) => (props.$current ? "var(--key-color)" : "#7b7b7b")};
  }
  font-size: 18px;
  font-weight: bold;
`;

const Sspan = styled.span`
  display: block;
  z-index: -1;
  height: 4px;
  position: absolute;
  top: 50%;
  left: 0;
  width: 32px;
  margin-left: 4px;
  transition: all 1s ease-in;
`;

export const WithRouterSample = () => {
  const location = useLocation();

  console.log();
  const [reverse, setReverse] = React.useState(false);
  const removeClass = () => {
    setReverse(false);
  };
  const addClass = () => {
    setReverse(true);
  };

  const [on, setOn] = useState(false);
  const toggleMenu = () => {
    setOn((on) => !on);
  };

  const [y, setY] = useState(document.scrollingElement.scrollHeight);
  const [scrollDirection, setScrollDirection] = useState(
    "you have not scrolled yet"
  );

  const handleNavigation = useCallback(
    (e) => {
      var windowWidth = window.matchMedia("screen and (max-width: 1440px)");

      if (windowWidth.matches) {
        if (y > window.scrollY) {
          document.getElementById("header").style.transform = "none";
        } else if (y < window.scrollY) {
          document.getElementById("header").style.transform = "none";
        }
      } else {
        if (y > window.scrollY) {
          document.getElementById("header").style.transform = "translateY(0)";
        } else if (y < window.scrollY) {
          document.getElementById("header").style.transform =
            "translateY(-80px)";
        }
      }

      setY(window.scrollY);
    },
    [y]
  );

  useEffect(() => {
    window.addEventListener("scroll", handleNavigation);

    return () => {
      window.removeEventListener("scroll", handleNavigation);
    };
  }, [handleNavigation]);

  return (
    <header id="header" className={`${reverse ? "reverse" : ""}`}>
      <div className={`head  ${on ? "on" : ""}`}>
        <figure>
          <Link to="/">
            <img src={Logo}></img>
          </Link>
        </figure>
        <div className={`navlist ${on ? "on" : ""}`}>
          <nav className="linklist">
            <ul>
              <li>
                <SLink
                  $current={location.pathname === "/"}
                  onClick={() => {
                    removeClass();
                    toggleMenu();
                  }}
                  test={location.pathname}
                  to="/"
                >
                  HOME
                </SLink>
              </li>

              <li>
                <SLink
                  $current={location.pathname === "/About"}
                  onClick={() => {
                    removeClass();
                    toggleMenu();
                  }}
                  test={location.pathname}
                  to="/About"
                >
                  ABOUT US
                </SLink>
              </li>

              <li>
                <SLink
                  $current={location.pathname === "/Approach"}
                  onClick={() => {
                    addClass();
                    toggleMenu();
                  }}
                  test={location.pathname}
                  to="/Approach"
                >
                  LIT APPROACH
                </SLink>
              </li>

              <li>
                <SLink
                  $current={location.pathname === "/Solutions"}
                  onClick={() => {
                    removeClass();
                    toggleMenu();
                  }}
                  test={location.pathname}
                  to="/Solutions"
                >
                  SOLUTIONS
                </SLink>
              </li>

              <li>
                <SLink
                  $current={location.pathname === "/Contact"}
                  onClick={() => {
                    addClass();
                    toggleMenu();
                  }}
                  test={location.pathname}
                  to="/Contact"
                >
                  CONTACT US
                </SLink>
              </li>
            </ul>
          </nav>
          <nav className="language">
            <ul>
              <li>
                <Link className="selected">
                  <span>EN</span>
                </Link>
              </li>
              <li>
                <Link
                  onClick={() => {
                    alert("준비중입니다");
                  }}
                >
                  <span>KO</span>
                </Link>
              </li>
              <li>
                <Link
                  onClick={() => {
                    alert("준비중입니다");
                  }}
                >
                  <span>JP</span>
                </Link>
              </li>
            </ul>
          </nav>
        </div>
        <div className="mb_btn">
          <button type="button" onClick={() => toggleMenu()}>
            <Sspan className="bt_01"></Sspan>
            <Sspan className="bt_02"></Sspan>
            <Sspan className="bt_03"></Sspan>
          </button>
        </div>
      </div>
    </header>
  );
};

export default WithRouterSample;
