import { debounce } from "lodash";
import React, { useLayoutEffect, useState } from "react";
import ParticleImage, { forces, Vector } from "react-particle-image";
import Title from "../images/approach_title.png";

var src = Title;

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      let copy = size;
      copy = [window.innerWidth, window.innerHeight];
      setSize(copy);
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);

  return size;
}

export default function PartyTitle(props) {
  const useState = React.useState;
  let PW = 10;
  const [width, height] = useWindowSize();

  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });
  React.useEffect(() => {
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    }, 1000);
    window.addEventListener("resize", debouncedHandleResize);

    return (_) => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  });

  var [w, setW] = useState(PW);
  var windowWidth = window.matchMedia("screen and (max-width: 800px)");

  if (windowWidth.matches) {
    PW = width;
  } else {
    PW = width / 1.8;
  }

  return (
    <div id="particle" className="partyTitle">
      <ParticleImage
        scale={1} //이미지 크기
        width={PW}
        height={height}
        entropy={2}
        maxParticles={1500} //파티클개수
        backgroundColor="rgba(31, 31, 31,0)" //백그라운드 색
        src={src} //이미지
        mouseMoveForce={(x, y) => forces.disturbance(x, y, 10)} //마우스에서 도망
        touchMoveForce={(x, y) => forces.disturbance(x, y, 100)}
        mouseDownForce={(x, y) => forces.disturbance(x, y, 500)} //클릭시 터짐
        particleOptions={{
          mass: () => 10,
          filter: ({ x, y, image }) => {
            const pixel = image.get(x, y);
            return pixel.r === 255; //흑백반전
          },
          color: getRandomColor, //파티클 색상
          friction: () => 0.7, //원상태로 복구되는 속도
          radius: () => 1.6, //dot 크기
          initialPosition: ({ canvasDimensions }) => {
            return new Vector(
              canvasDimensions.width / 100,
              canvasDimensions.height / 100
            );
          },
        }}
      />
    </div>
  );
}

function getRandomColor() {
  return "#" + Math.floor(Math.random() * 16777215).toString(16);
}
