import React from "react";
import Party from "../components/Party.js";
import Tablet from "../images/tablet_img.jpg";
import Card01 from "../images/about_card_01.png";
import Card02 from "../images/about_card_02.png";
import Card03 from "../images/about_card_03.png";
import Solution01 from "../images/solution_01.png";
import Solution02 from "../images/solution_02.png";
import Solution03 from "../images/solution_03.png";
import Solution04 from "../images/solution_04.png";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";

const useState = React.useState;
let temp = 10;
let maxParticle = 1000;

const activeClassEvent = (num) => {
  for (let i = 1; i <= 6; i++) {
    if (i == num) {
      document.getElementById("list_0" + i).classList.add("active");
      document.getElementById("disc").classList.add("disc_0" + i);
    } else {
      document.getElementById("list_0" + i).classList.remove("active");
      document.getElementById("disc").classList.remove("disc_0" + i);
    }
  }
};

const scrollToTop = () => {
  window.scrollTo({
    top: 0,
    behavior: "smooth",
  });
};

function About() {
  var windowWidth = window.matchMedia("screen and (max-width: 800px)");

  if (windowWidth.matches) {
    maxParticle = 1000;
    temp = 0.5;
  } else {
    maxParticle = 3000;
    temp = 0.85;
  }
  var [num, setcount] = useState(temp);
  var [n, setN] = useState(maxParticle);
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  window.addEventListener("scroll", (e) => {
    const tablet = document.getElementById("tablet");
    const box = document.getElementById("hide");

    var h = document.getElementById("path_00").scrollHeight;

    var y = window.scrollY,
      val = (y / h) * 100,
      v = 2000 - (2000 * val) / 100,
      v1 = 3000 - (800 * val) / 100;

    if (v >= 0) {
      document.getElementById("path_00").style.strokeDashoffset = v;
    } else if (v1 >= 0) {
      document.getElementById("path_00").style.strokeDashoffset = 0;
      document.getElementById("path_01").style.strokeDashoffset = v1;
    } else {
      document.getElementById("path_01").style.strokeDashoffset = 0;
    }

    let s2h = document.getElementById("section_02").scrollHeight,
      val_2 = (y / s2h) * 100;
    var windowWidth_01 = window.matchMedia("screen and (max-width: 1440px)");
    if (windowWidth_01.matches) {
      if (val_2 > 50) {
        tablet.style.opacity = "1";
        tablet.style.transform = "translateY(0)";
      } else {
        tablet.style.opacity = "0";
        tablet.style.transform = "translateY(10%)";
      }
    } else {
      if (val_2 > 190) {
        box.style.opacity = "1";
        box.style.transform = "translateY(0)";
      } else {
        box.style.opacity = "0";
        box.style.transform = "translateY(10%)";
      }

      if (val_2 > 90) {
        tablet.style.opacity = "1";
        tablet.style.transform = "translateY(0)";
      } else {
        tablet.style.opacity = "0";
        tablet.style.transform = "translateY(10%)";
      }

      if (125 > val_2 && val_2 > 115) {
        activeClassEvent(1);
      } else if (135 > val_2 && val_2 > 125) {
        activeClassEvent(2);
      } else if (145 > val_2 && val_2 > 135) {
        activeClassEvent(3);
      } else if (155 > val_2 && val_2 > 145) {
        activeClassEvent(4);
      } else if (165 > val_2 && val_2 > 155) {
        activeClassEvent(5);
      } else if (175 > val_2 && val_2 > 165) {
        activeClassEvent(6);
      }
    }
  });
  return (
    <>
      <div id="about">
        <section className="section_01">
          <div className="section_01_inner">
            <div className="path">
              <svg
                id="path_00"
                data-name="path 1"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 597.26 336.84"
              >
                <path
                  className="cls-1"
                  d="M199.02,143.52L1,89.38m0,158.07l198.02-54.13m0-24.9H1m397.24,24.9l198.02,54.13m0-158.07l-198.02,54.13m198.02,24.9h-198.02m-99.61-49.8V1m-24.9,117.61L224.77,1m24.06,117.61L144.38,1m-82.8,0L223.92,118.61M1,19.6L199.02,118.61M372.49,1l-48.96,117.61m24.9,0L452.88,1m-79.54,117.61L535.68,1m-237.05,217.22v117.61m-24.9-117.61l-48.96,117.61m-80.39,0l104.44-117.61m-24.9,0L61.58,335.84m261.95-117.61l48.96,117.61m-24.06-117.61l104.44,117.61m-79.54-117.61l162.34,117.61M398.24,118.61L596.26,19.6m0,297.63l-198.02-99.01m-199.22,0L1,317.23M199.02,118.61v99.61h199.22V118.61H199.02m209.15-4.96H189.09v109.54h219.07V113.65Zm14.1-7.05H174.99v123.64h247.28V106.6Zm19.4-9.7H155.59V239.94H441.67V96.9Zm25.24-12.62H130.35V252.56H466.91V84.28Zm37-18.5H93.35v205.28H503.91V65.78Zm50.34-25.17H43.01v255.63h511.25V40.6ZM596.26,1H1V335.84H596.26V1ZM199.06,193.36h199.23m-199.27-24.94h199.22m-199.29-24.94h199.23m-174.43-24.93v99.61m25.07-99.71v99.61m25.04-99.54v99.61m24.76-99.51v99.61m24.76-99.61v99.61m25.04-99.64v99.61m25.04,.07V118.65"
                />
              </svg>
            </div>
            <div id="bgParticle" className="bg_particle">
              <Party scale={num} maxParticles={n} />
              <div className="title_con">
                <div className="title">Who We Are</div>
                <div className="info">
                  “ Technology company re-shaping the asset management industry
                  ”
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="section_02" className="section_02">
          <div className="path">
            <svg
              id="path_01"
              data-name="path 2"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1922.35 1016.38"
              className="sticky_svg"
            >
              <path
                className="cls-1"
                d="M961.17,0V300M722.93,0V300M463.65,0V300M196.57,0V300M1199.4,0V300M1458.69,0V300M1725.76,0V300m-764.6,45.29V857.48m-238.24-512.19V857.48M463.65,345.29V857.48M196.57,345.29V857.48M1199.4,345.29V857.48m259.28-512.19V857.48m267.08-512.19V857.48m195.4-512.19h0M961.17,857.53v157.47m238.24-157.52l74.7,157.95m184.58-157.95l147.85,158.21m119.22-158.21l196,140.81m-.6-396.87H1.17m0-256.12h0M961.17,857.53v156.48m-238.24-156.52l-74.7,157.95m-184.58-157.95l-147.87,158.22m-119.21-158.22L.58,998.27m.58-140.79H1921.17m0-512.19H1.17m0,563.29H1921.17m0-563.29H1.17m0,635.82H1921.17m0-635.82H1.17m1920,0H1.17"
              />
            </svg>
          </div>
          <div className="blur_con"></div>

          <div id="tablet" className="tablet inner_wrap">
            <div className="title_con">
              <div className="title">About LIT</div>
            </div>
            <ul id="disc" className="info">
              <li id="list_01">
                <span>
                  Founded in 2019 by LINE Group, a leading IT company in Japan
                </span>
              </li>
              <li id="list_02">
                <span>
                  Managed by global hedge fund veterans and PhD scientists from
                  global top schools
                </span>
              </li>
              <li id="list_03">
                <span>
                  Systematic Investment process driven by rigorous mathematical
                  and statistical method
                </span>
              </li>
              <li id="list_04">
                <span>Excellent IT infrastructure support from LINE</span>
              </li>
              <li id="list_05">
                <span>
                  The flagship fund, LIT Systematic Global Macro Program was
                  launched in Singapore in January 2021
                </span>
              </li>
              <li id="list_06">
                <span>
                  Total of 20+ employees in Japan and Korea, including 7 PhD
                  holders and 2 CFA® charterholders.
                </span>
              </li>
            </ul>
          </div>
        </section>
        <section className="section_03">
          <div className="path">
            <svg
              id="path_02"
              data-name="path 3"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1920 782.01"
              className="sticky_svg"
            >
              <path
                className="cls-1"
                d="M1272.93,.43l30.95,65.45m362.41,0L1605.39,.71M960,0V65.87M647.07,.43l-30.95,65.45M314.63,.69l-60.92,65.19m-253.71,0H1920m-960,0V782.01M1303.88,65.87V782.01M616.12,65.87V782.01M253.71,65.87V782.01M1666.29,65.87V782.01"
              />
            </svg>
          </div>
          <div id="hide" className="width_con inner_wrap">
            <div className="box_con">
              <ul>
                <li>
                  <figure>
                    <img src={Solution01} />
                  </figure>
                  <div className="title">Technical</div>
                </li>
                <li>
                  <figure>
                    <img src={Solution03} />
                  </figure>
                  <div className="title">Fundamental</div>
                </li>
                <li>
                  <figure>
                    <img src={Solution02} />
                  </figure>
                  <div className="title">AI</div>
                </li>
                <li>
                  <figure>
                    <img src={Solution04} />
                  </figure>
                  <div className="title">Intraday</div>
                </li>
              </ul>
            </div>
          </div>
          <div className="blur_con"></div>
        </section>
        <Link onClick={scrollToTop} id="gotop" className="gotop">
          <span>TOP</span>
        </Link>
      </div>
      <Footer />
    </>
  );
}
export default About;
