import React, { useEffect, useLayoutEffect } from "react";
import Party from "../components/Party.js";
import { Controller, Scene } from "react-scrollmagic";
import { Timeline, Tween } from "react-gsap";
import Logo from "../images/logo.png";
import Home01 from "../images/home_01.png";
import { Link } from "react-router-dom";

const useState = React.useState;

let flag = false;
let temp = 10;
let maxParticle = 1000;

function Home() {
  var [num, setcount] = useState(temp);

  temp = 1;
  var windowWidth = window.matchMedia("screen and (max-width: 800px)");

  if (windowWidth.matches) {
    maxParticle = 1000;
    if (!flag) {
      flag = true;
      setTimeout(() => {
        setcount(num - 9.5);
      }, 1250);
    }
  } else {
    maxParticle = 3000;
    if (!flag) {
      setN = n * 2;
      flag = true;
      setTimeout(() => {
        setcount(num - 9.15);
      }, 1250);
    }
  }
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  var [n, setN] = useState(maxParticle);
  let timer = setTimeout(() => {
    var y = window.scrollY;
    if (windowWidth.matches) {
      if (y > 1) {
        setcount(0.5 + y * 0.001);
      } else if (10 > y && y >= 0) {
        setcount(0.5);
      }
    } else {
      if (y > 1) {
        setcount(1 + y * 0.001);
        document.querySelector(".path").style.transform =
          "scale(" + (1 + y * 0.0001) + ")";
      } else if (10 > y && y >= 0) {
        setcount(0.85);
        document.getElementById(".path").style.transform = "scale(1)";
      }
    }
  });
  clearTimeout(timer);

  window.addEventListener("scroll", (e) => {
    var h = document.getElementById("home").scrollHeight;
    let y = window.scrollY,
      val = y / h;

    clearTimeout(timer);
    timer = setTimeout(() => {
      if (windowWidth.matches) {
        if (y > 1) {
          setcount(0.5 + y * 0.001);
          document.querySelector(".path").style.transform =
            "scale(" + (1 + val) + ")";
        } else if (10 > y && y >= 0) {
          setcount(0.5);
          document.querySelector(".path").style.transform = "scale(1)";
        }
      } else {
        if (y > 1) {
          setcount(1 + y * 0.001);
          document.querySelector(".path").style.transform =
            "scale(" + (1 + val) + ")";
        } else if (10 > y && y >= 0) {
          setcount(0.85);
          document.querySelector(".path").style.transform = "scale(1)";
        }
      }
    }, 100);
  });

  return (
    <>
      <Controller>
        <div id="home">
          <section className="section_01 inner_wrap">
            <div id="bgParticle" className="bg_particle">
              <div className="path">
                <svg
                  id="grid"
                  data-name="grid"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 598.32 336.4"
                >
                  <path
                    className="cls-1"
                    d="M224.18,143.84h-24.99v-24.99h24.99v24.99Zm0,0h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm24.99-74.98h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm0,24.99h-24.99v24.99h24.99v-24.99Zm-199.94-49.99L.45,89.52m0,158.64l198.74-54.33m0-24.99H.45m398.69,24.99l198.74,54.33m0-158.64l-198.74,54.33m0,24.99h198.74m-298.71-49.99V.81m-24.99,118.04L225.03,.81m24.14,118.04L144.35,.81m79.83,118.04L61.25,.81M199.19,118.85L.45,19.48M324.15,118.85L373.29,.81m-24.14,118.04L453.97,.81m-79.83,118.04L537.07,.81m-237.91,218.01v116.76m-24.99-116.76l-48.6,116.76m23.61-116.76l-103.69,116.76m-82.47,0l161.17-116.76m148.58,116.76l-48.6-116.76m24.99,0l103.69,116.76m-78.7-116.76l161.17,116.76M399.13,118.85L597.88,19.48m0,298.71l-198.74-99.37m-199.94,0L.45,318.19M399.13,118.85H199.19v99.97h199.94V118.85Zm9.96-4.98H189.23v109.94h219.87V113.87Zm14.16-7.08H175.07v124.09h248.18V106.79Zm19.47-9.73H155.6V240.62H442.72V97.06Zm25.33-12.67H130.27V253.28H468.06V84.39Zm37.14-18.57H93.13v206.03H505.19V65.82Zm50.53-25.26H42.61V297.11H555.72V40.56Z"
                  />
                </svg>
              </div>
              <Party scale={num} maxParticles={n} />
            </div>
            <Scene triggerHook="onLeave" duration={2000} pin>
              <Timeline
                target={
                  <div id="scaleTitle" className="title_con">
                    <div className="title">LINE Investment Technologies</div>
                    <div className="sub_title">
                      LINE Investment Technologies is a quantitative investment
                      manager
                      <br />
                      focused on systematic global macro investment.{" "}
                    </div>
                  </div>
                }
              >
                <Tween
                  from={{ opacity: 1, scale: 1 }}
                  to={{ opacity: 0, scale: 0 }}
                ></Tween>
              </Timeline>
            </Scene>
            <div className="none"></div>
            <Scene triggerHook="onLeave" duration={2000} pin>
              <Timeline
                target={
                  <div id="scaleInfo_01" className="info_con info_con_01">
                    <div className="info">
                      <div className="title_00">
                        LIT is a global systematic
                        <br />
                        hedge fund manager
                        <br />
                        supported by LINE Corporation.
                      </div>
                      <div className="info_00">
                        Our advanced AI makes the decision,
                        <br />
                        the robust LINE platform execute the transactions,
                        <br />
                        human monitors the operations.{" "}
                      </div>
                    </div>
                  </div>
                }
              >
                <Tween
                  from={{ opacity: 0, y: 30 }}
                  to={{ opacity: 1, y: 1 }}
                ></Tween>
                <Tween to={{ opacity: 0, scale: 0 }}></Tween>
              </Timeline>
            </Scene>
            <div className="none"></div>
            <Scene triggerHook="onLeave" duration={2000} pin>
              <Timeline
                target={
                  <div id="scaleInfo_02" className="info_con info_con_02">
                    <div className="info">
                      <div className="top">
                        <div className="title_01">Our Product</div>
                        <div className="info_01">
                          LIT Fund (Genesis) is a systematic global macro fund
                          with a balanced combination of fundamental, technical,
                          and machine learning strategies.
                        </div>
                      </div>
                      <div className="bot">
                        <div className="title_02">Our People</div>
                        <div className="info_02">
                          Advance AI program required advance investment
                          professionals. The fund is managed by investment
                          professionals with PhDs in quantitative fields and
                          with experiences from leading global hedge funds.
                        </div>
                      </div>
                    </div>
                    <div className="image">
                      <figure>
                        <img src={Home01}></img>
                      </figure>
                    </div>
                  </div>
                }
              >
                <Tween
                  from={{ opacity: 0, y: 30 }}
                  to={{ opacity: 1, y: 1 }}
                ></Tween>
                <Tween to={{ opacity: 0, scale: 0 }}></Tween>
              </Timeline>
            </Scene>
            <div className="none"></div>
            <div className="none"></div>
            <div className="none"></div>
            <div id="scaleEnd" className="ending">
              <img src={Logo}></img>
              <div className="info">
                Re-shaping the asset management industry.
              </div>
              <div className="etc">2022 by LINE INVESTMENT TECHNOLOGY.</div>
              <Link onClick={scrollToTop} id="gotop" className="gotop">
                <span>TOP</span>
              </Link>
            </div>
          </section>
        </div>
      </Controller>
    </>
  );
}

export default Home;
